<template>
  <section style="margin-left:15px;margin-right:15px;margin-top: 15px;">
    <el-card class="box-card">
      <!--工具条-->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters" @submit.native.prevent>
              <el-form-item>
                  <el-input v-model="filters.name" placeholder="名称" @keyup.enter.native="getCampaignList"></el-input>
              </el-form-item>
              <el-form-item>
                  <el-button type="primary" @click="getCampaignList">查询</el-button>
              </el-form-item>
              <el-form-item>
                  <el-button type="primary" @click="handleAdd" icon="el-icon-plus">数据策略</el-button>
              </el-form-item>
          </el-form>
      </el-col>

      <!--列表-->
      <el-table :data="campaigns" highlight-current-row v-loading="listLoading" @selection-change="selsChange">
        <el-table-column>
          <template slot-scope="scope">
            <el-card class="box-card" shadow="hover">
          <el-descriptions class="margin-top" title="" :column="3" :size="size">
            <template slot="title">
            <router-link  :to="{ name: '活动管理', params: {campaign_id: scope.row.id}}"
                style="cursor:pointer;color: #0000FF;text-decoration:underline">
                      {{ scope.row.name }}
            </router-link>
          </template>

             <template slot="extra">
                <el-button type="text" size="small" style="color:#808B96" @click="handleDel(scope.$index, scope.row)">删除</el-button>
            </template>
           <el-descriptions-item label="活动编号">{{scope.row.id}}</el-descriptions-item>
           <el-descriptions-item label="创建人">{{scope.row.author}}</el-descriptions-item>
           <el-descriptions-item label="创建时间">{{scope.row.creation_date}}</el-descriptions-item>
           <el-descriptions-item label="标签">
            <el-tag size="small" type="info">机器学习/强化学习</el-tag>
           </el-descriptions-item>
           <el-descriptions-item v-if="scope.row.data_campaign_type==='recs'" label="活动类型">策略推荐</el-descriptions-item>
           <el-descriptions-item v-else-if="scope.row.data_campaign_type==='promotions'" label="活动类型">个性化促销</el-descriptions-item>
           <el-descriptions-item v-else label="活动类型">通用</el-descriptions-item>
           <el-descriptions-item label="活动状态"><el-tag size="small">进行中</el-tag></el-descriptions-item>
           <el-descriptions-item label="活动描述">{{scope.row.description}}</el-descriptions-item>
          </el-descriptions>
        </el-card>
      </template>
    </el-table-column>
  </el-table>
  <!--工具条-->
  <el-col :span="24" class="toolbar">
    <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :page-count="total" style="float:right;">
    </el-pagination>
  </el-col>
</el-card>

      <!--编辑界面-->
      <el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false" style="width: 75%; left: 12.5%">
          <el-form :model="editForm" label-width="80px"  :rules="editFormRules" ref="editForm">
              <el-form-item label="项目名称" prop="name">
                  <el-input v-model="editForm.name" auto-complete="off"></el-input>
              </el-form-item>
              <el-row :gutter="24">
                  <el-col :span="12">
                      <el-form-item label="类型" prop='type'>
                          <el-select v-model="editForm.proj_type" placeholder="请选择">
                              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                              </el-option>
                          </el-select>
                      </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="版本号" prop='version'>
                          <el-input v-model="editForm.version" auto-complete="off"></el-input>
                      </el-form-item>
                  </el-col>
              </el-row>
              <el-form-item label="描述" prop='description'>
                  <el-input type="textarea" :rows="6" v-model="editForm.description"></el-input>
              </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
              <el-button @click.native="editFormVisible = false">取消</el-button>
              <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
          </div>
      </el-dialog>

      <!--新增界面-->
      <el-dialog title="新增数据策略" :visible.sync="addFormVisible" :close-on-click-modal="false" style="width: 75%; left: 12.5%">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
              <el-form-item label="项目名称" prop="name">
                  <el-input v-model.trim="addForm.name" auto-complete="off"></el-input>
              </el-form-item>
              <el-row :gutter="24">
                  <el-col :span="12">
                      <el-form-item label="类型" prop='type'>
                          <el-select v-model="addForm.type" placeholder="请选择">
                              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                              </el-option>
                          </el-select>
                      </el-form-item>
                  </el-col>
                  <el-col :span="12">
                      <el-form-item label="版本号" prop='version'>
                          <el-input v-model.trim="addForm.version" auto-complete="off"></el-input>
                      </el-form-item>
                  </el-col>

              </el-row>
              <el-form-item label="描述" prop='description'>
                  <el-input type="textarea" :rows="6" v-model="addForm.description"></el-input>
              </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
              <el-button @click.native="addFormVisible = false">取消</el-button>
              <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
          </div>
      </el-dialog>
      <br>
  </section>
</template>

<script>
// import NProgress from 'nprogress'
import $ from 'jquery'
import moment from 'moment'
import { test, addCampaign, getCampaigns, delCampaign, updateProject } from '@/api/api'
// import ElRow from "element-ui/packages/row/src/row";
export default {
// components: {ElRow},
  data () {
    return {
      size: '',
      users: {},
      filters: {
        name: ''
      },
      campaigns: [],
      total: 0,
      page: 1,
      listLoading: false,
      sels: [], // 列表选中列
      editFormVisible: false, // 编辑界面是否显示
      editLoading: false,
      options: [{ label: '策略推荐', value: 'recs' },
        { label: '个性化促销', value: 'promotions' },
        { label: '通用', value: 'general' }],
      editFormRules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        proj_type: [
          { required: true, message: '请选择类型', trigger: 'blur' }
        ],
        version: [
          { required: true, message: '请输入版本号', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入描述', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 编辑界面数据
      editForm: {
        name: '',
        version: '',
        proj_type: '',
        description: ''
      },
      addFormVisible: false, // 新增界面是否显示
      addLoading: false,
      addFormRules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请选择类型', trigger: 'blur' }
        ],
        version: [
          { required: true, message: '请输入版本号', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入版本号', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 新增界面数据
      addForm: {
        name: '',
        version: '',
        type: '',
        description: ''
      }
    }
  },
  methods: {
  // row[column.property] 能读取到该行该列的数据。代码中实现了时间格式的转换
    formatTime2 (row, column) {
      const date = new Date(row[column.property])
      return date.getFullYear() + '-' +
                  date.getMonth() + '-' +
                  date.getDate() + ' ' +
                  date.getHours() + ':' +
                  date.getMinutes() + ':' +
                  date.getSeconds()
    },
    formatTime: function (row, column) { // 日期格式化
      var date = row[column.property]
      if (typeof date === 'undefined') {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    getUserInfo () {
      let self = this
      let param = { project_id: self.$route.params.project_id }
      $.ajax({
        type: 'get',
        // url: test+"/api/api/api_info",
        url: test + '/api/user/get_user_meta',
        async: true,
        data: param,
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.users = data.data.users
            console.log(self.users, 'self.users')
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    // 获取项目列表
    getCampaignList () {
      this.listLoading = true
      let self = this
      let params = { page: self.page,
        user_key: JSON.parse(sessionStorage.getItem('name')),
        name: self.filters.name }
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getCampaigns(headers, params).then((res) => {
        self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.total = data.total
          self.campaigns = data.data
          console.log(self.campaigns, 'self.campaigns')
          console.log(self.total, 'self.total')
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该活动吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = { ids: [row.id],
          user_name: JSON.parse(sessionStorage.getItem('name')) }
        let header = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        delCampaign(header, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getCampaignList()
        })
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.getCampaignList()
    },
    // 显示编辑界面
    handleEdit: function (index, row) {
      this.editFormVisible = true
      this.editForm = Object.assign({}, row)
    // console.log("row",row)
    },
    // 显示新增界面
    handleAdd: function () {
      this.addFormVisible = true
    },
    // 编辑
    editSubmit: function () {
      let self = this
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.editLoading = true
            // NProgress.start();
            let params = {
              project_id: self.editForm.id,
              name: self.editForm.name,
              type: self.editForm.proj_type,
              version: self.editForm.version,
              description: self.editForm.description,
              user_key: JSON.parse(sessionStorage.getItem('name'))
            }
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            updateProject(header, params).then(_data => {
              let { msg, code } = _data
              self.editLoading = false
              if (code === '999999') {
                self.$message({
                  message: '修改成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['editForm'].resetFields()
                self.editFormVisible = false
                self.getCampaignList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
              }
            })
          })
        }
      })
    },
    // 新增
    addSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          let self = this
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.addLoading = true
            // NProgress.start();
            let params = JSON.stringify({
              name: self.addForm.name,
              type: self.addForm.type,
              description: self.addForm.description,
              user: JSON.parse(sessionStorage.getItem('name'))
            })
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            addCampaign(header, params).then(_data => {
              let { msg, code } = _data
              self.addLoading = false
              if (code === '999999') {
                self.$message({
                  message: '添加成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getCampaignList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getCampaignList()
              }
            })
          })
        }
      })
    },
    selsChange: function (sels) {
      this.sels = sels
    }
  },
  mounted () {
    this.getCampaignList()
    // this.getUserInfo()
  /* var token = sessionStorage.getItem("token");
          var user = sessionStorage.getItem("name");
          if (token && user) {
              user = JSON.parse(user);
              this.sysUserName = user || "";
          } else {
              sessionStorage.removeItem("token");
              this.$router.push("/login");
          } */
  }
}
</script>

<style>
